import React from "react";

function Header(props) {
  return (
    <div className="header">
      <h1 className="bumbletext">freeCodeCamp Certifications</h1>
      <h3 className="bumbletext">{props.title}</h3>
      <a
        href={`https://www.freecodecamp.org/certification/boutchersj/${
          props.url
        }`}
        target="_blank"
        rel="noopener noreferrer"
      >
        <img
          src={props.image}
          className="certification"
          alt="freeCodeCamp certification"
        />
      </a>
    </div>
  );
}

export default Header;
