import React, {useState} from "react";

function Project(props) {
  const [maxScreen] = useState(window.matchMedia("(max-width: 812px)"));

  const mobileMode = event => {
    const { id } = event.target;
    const img = document.getElementById(id);
    const links = document.getElementById(id).parentElement.querySelectorAll('a');
    if (maxScreen.matches) {
      // If media query matches
      img.classList.toggle("blur");
      for (let i = 0; i < links.length; i++){
      links[i].classList.contains('hidden') && links[i].classList.remove('hidden');
      }
    }
  };
  const blur = event => {
    const { id } = event.target;
    const links = document.getElementById(id).parentElement.querySelectorAll('a');
    for (let i = 0; i < links.length; i++){
      links[i].classList.contains('hidden') && links[i].classList.remove('hidden');
    }
    const img = document.getElementById(id).parentElement.firstChild;
    img.classList.toggle("blur");
    
  };
  const showLinks = event => {
    const { id } = event.target;
    const links = document.getElementById(id).parentElement.querySelectorAll('a')
    for (let i = 0; i < links.length; i++){
      links[i].classList.remove('hidden')
    }
  }
  const hideLinks = event => {
    const { id } = event.target;
    const links = document.getElementById(id).parentElement.querySelectorAll('a')
    for (let i = 0; i < links.length; i++){
      links[i].classList.add('hidden')
    }
  }
  return (
    <div className="project">
      <div className="project-container">
        <img
          id={props.id}
          className="thumbnail"
          src={props.image}
          alt={props.alt}
          onTouch={mobileMode}
          onMouseOver={showLinks}
          onMouseOut={hideLinks}
        />
        <a
          id={`${props.id}-repo`}
          className={props.live ? "repo hidden" : "script hidden"}
          href={props.repo}
          target="_blank"
          rel="noopener noreferrer"
          onMouseOver={blur}
          onMouseOut={blur}
        >
          View Repo
        </a>
        {props.live && (
          <a
            id={`${props.id}-live`}
            className="live hidden"
            href={props.live}
            target="_blank"
            rel="noopener noreferrer"
            onMouseOver={blur}
            onMouseOut={blur}
          >
            View Live
          </a>
        )}
      </div>
      <h4 className="bumbletext">{props.title}</h4>
    </div>
  );
}

export default Project;
