import React from 'react'
import Navbar from './Navbar'
import '../styles/Contact.css'

class Contact extends React.Component {
    constructor(){
        super()
        this.state = {
            name: '',
            company: '',
            email: '',
            subject: '',
            message: ''
        }
        this.handleChange = this.handleChange.bind(this)
    }
    handleChange(event){
        const { name, value } = event.target
        this.setState({
            [name]: value
        })
    }
    render(){
        return (
            <div id="page">
                <Navbar
                    toggleDropdown={this.props.toggleDropdown}
                    changePage={this.props.changePage}
                />
                <br />
                <br />
                <div id="header">
                <h1 id='title'>Send Me An Email!</h1>
                </div>
                <br /><br />
                <div id='form'>

                    <label htmlFor='name'>Name</label>
                    <input 
                        type='text' 
                        placeholder="Your name" 
                        name='name' 
                        value={this.state.name}
                        onChange={this.handleChange}
                        autoFocus 
                    />


                    <label htmlFor='company'>Company</label>
                    <input 
                        type='text' 
                        placeholder="Your company's name" 
                        name='company' 
                        value={this.state.company}
                        onChange={this.handleChange}
                    />

                    <label htmlFor='email'>Email</label>
                    <input 
                        type='email' 
                        placeholder="Your email" 
                        name='email' 
                        value={this.state.email}
                        onChange={this.handleChange}
                    />

                    <label htmlFor='subject'>Subject</label>
                    <input 
                        type='text' 
                        placeholder="What is this about?" 
                        name='subject' 
                        value={this.state.subject}
                        onChange={this.handleChange}
                        required
                    />

                    <label htmlFor='message'>Message</label>
                    <textarea 
                        placeholder='Write a short message...'
                        name='message'
                        value={this.state.message}
                        onChange={this.handleChange}
                        wrap='hard'
                        cols='50'
                        required
                    />

                    <a 
                        href={'mailto:boutchersj@gmail.com?' +
                        'subject=' + encodeURIComponent(this.state.subject) +
                        '&body=' + encodeURIComponent(this.state.message) + 
                        '%0D%0D' + encodeURIComponent(this.state.name) + 
                        '%0D' + encodeURIComponent(this.state.company) + 
                        '%0D' + encodeURIComponent(this.state.email) + '%0D'}
                        target='_blank'
                        rel='noopener noreferrer'
                    >
                        <button id='send-button'>Send</button>
                    </a>
                </div>
            </div>
        )
    }
}

export default Contact