import React, {useEffect} from "react";
import Navbar from "./Navbar";
import "../styles/Portfolio.css";

function Portfolio(props) {
  useEffect(() => {
    document.getElementById('page').scrollTop = 0;
  })
  return (
    <div id="page">
      <Navbar
        toggleDropdown={props.toggleDropdown}
        changePage={props.changePage}
      />
      <br />
      <br />
      <div id="header">
        <h1 id="title">Steven Boutcher</h1>
        <h3 id="subtitle">Front End Web Developer</h3>
      </div>
      <div id="body">
        <p id="about">
          &nbsp;&nbsp;&nbsp;I'm just a guy who studied to be a teacher but ended
          up wanting to be a web developer. So a year before graduation, I
          started to put in the work. I studied HTML, CSS, and JavaScript before
          learning React.js and creating 6 projects in it, this portfolio
          website included. Overall, my work consists of more than 15 web
          projects and apps, all stored here.
          <br />
          <br />
          &nbsp;&nbsp;&nbsp;I've always been a curious person who likes to keep
          moving. I spent 10 years learning the violin, 8 more learning parkour,
          and recently I've learned how to drive stick shift. I've worked in all
          kinds of customer service and did a stint at Amazon packing boxes, and
          I've lived in Spain for 105 days studying the language. Would have
          done the same with Japanese had I had the time.
          <br />
          <br />
          &nbsp;&nbsp;&nbsp;All of that has one thing in common: It has nothing
          to do with web development, and everything to do with the kind of
          person that makes a great web developer over time. I have potential
          because I have drive, curiosity, passion, and a thirst for knowledge.
          I don't mind putting in the work to get what I want, and I can
          communicate and function well on a team.
          <br />
          <br />
          &nbsp;&nbsp;&nbsp;I'm confident in my ability to succeed as a web
          developer.
        </p>
      </div>
    </div>
  );
}

export default Portfolio;
