import React, {useEffect} from "react";
import "../styles/CertPage.css";
import data from '../data.json'
import Header from "./Header";
import Project from "./Project";
import certone from "../images/1/certone.jpg";
import tribute from "../images/1/tribute.jpg";
import survey from "../images/1/survey.jpg";
import productLanding from "../images/1/product-landing.jpg";
import docsPage from "../images/1/docs-page.jpg";
import portfolio from "../images/1/portfolio.jpg";
import Navbar from "./Navbar";

function CertOne(props) {
  let i = -1;
  const images = [tribute, survey, productLanding, docsPage, portfolio]
  useEffect(() => {
    document.getElementById('page').scrollTop = 0;
  }, [])
  const projects = data.certOne.map((p) => {
    i++
    return <Project
    area={`item${i}`}
    id={p.id}
    live={p.live}
    repo={p.repo}
    image={images[i]}
    alt={p.alt}
    title={p.title}
    />
  })
  return (
    <div id='page'>
      <Navbar 
        toggleDropdown={props.toggleDropdown}
        changePage={props.changePage}
      />
      <br /><br />
      <div className="container">
        <Header
          title="Responsive Web Design"
          url="responsive-web-design"
          image={certone}
        />

        <div>
          {projects}
        </div>
      </div>
    </div>
  );
}

export default CertOne;
