import React, {useEffect} from "react";
import "../styles/CertPage.css";
import data from '../data.json'
import Navbar from "./Navbar";
import Header from "./Header";
import Project from "./Project";
import certthree from "../images/3/certthree.jpg";
import randomQuoteGenerator from "../images/3/random-quote-generator.jpg";
import markdownPreviewer from "../images/3/markdown-previewer.jpg";
import drumMachine from "../images/3/drum-machine.jpg";
import javascriptCalculator from "../images/3/javascript-calculator.jpg";
import pomodoroClock from "../images/3/pomodoro-clock.jpg";


function CertThree(props) {
  let i = -1;
  const images = [randomQuoteGenerator, markdownPreviewer, drumMachine, javascriptCalculator, pomodoroClock]
  const projects = data.certThree.map((p) => {
    i++
    return <Project 
    area={`item${i}`}
    id={p.id}
    live={p.live}
    repo={p.repo}
    image={images[i]}
    alt={p.alt}
    title={p.title}
    />
  })
  useEffect(() => {
    document.getElementById('page').scrollTop = 0;
  })
  return (
    <div id='page'>
      <Navbar 
          toggleDropdown={props.toggleDropdown}
          changePage={props.changePage}
      />
      <br /><br />
      <div className="container">
        <Header
          title="Front End Libraries"
          url="front-end-libraries"
          image={certthree}
        />
        <div>
            {projects}
        </div>
      </div>
    </div>
  );
}

export default CertThree;
