import React from "react";
import Portfolio from "./Portfolio";
import CertOne from "./CertOne";
import CertTwo from "./CertTwo";
import CertThree from "./CertThree";
import Contact from "./Contact";

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      certOne: false,
      certTwo: false,
      certThree: false,
      contactForm: false,
      home: false
    };
    this.toggleDropdown = this.toggleDropdown.bind(this);
    this.changePage = this.changePage.bind(this);
  }
  toggleDropdown(event) {
    const { id } = event.target;
    let menuOptions = ["proj", "social", "pub"]; //Create a list of the menu option id's
    menuOptions.splice(menuOptions.indexOf(id), 1); //Exclude the clicked menu option from this list
    const content = document.getElementById(id).nextSibling;
    const option2 = document.getElementById(menuOptions[0]).nextSibling;
    const option3 = document.getElementById(menuOptions[1]).nextSibling; //Reference all 3 menu options' dropdown content
    content.className = content.className === "hide" ? "show" : "hide"; //Show or hide the clicked element's content
    option2.className = "hide";
    option3.className = "hide";
     //Hide the other menu options' content
  }

  changePage(event) {
    const { id } = event.target;
    let pages = ["certOne", "certTwo", "certThree", 'contactForm', 'home'];
    pages.splice(pages.indexOf(id), 1);
    const other1 = pages[0];
    const other2 = pages[1];
    const other3 = pages[2];
    const other4 = pages[3];
    this.setState({
      [id]: true,
      [other1]: false,
      [other2]: false,
      [other3]: false,
      [other4]: false
    });
  }

  render() {
    return this.state.certOne === true ? (
      <CertOne 
        changePage={this.changePage} 
        toggleDropdown={this.toggleDropdown}
      />
    ) : this.state.certTwo === true ? (
      <CertTwo 
        changePage={this.changePage} 
        toggleDropdown={this.toggleDropdown}
      />
    ) : this.state.certThree === true ? (
      <CertThree 
        changePage={this.changePage}
        toggleDropdown={this.toggleDropdown}
      />
    ) : this.state.contactForm === true ? (
      <Contact
        changePage={this.changePage}
        toggleDropdown={this.toggleDropdown}
      />
    ) : this.state.home === true ? (
      <Portfolio
        changePage={this.changePage}
        toggleDropdown={this.toggleDropdown}
      />
    ) : (
      <Portfolio
        changePage={this.changePage}
        toggleDropdown={this.toggleDropdown}
      />
    );
  }
}

export default App;
